import { Fragment } from 'react';
import { withRouter } from 'react-router-dom';

import localStorageHelper from '../../helpers/localStorage';

function Player(props) {
    const { video, subtitle } = props;

    const handleVideoLoad = () => {
        localStorageHelper.addVideo(video.url);
        console.log('h');
    };

    return (
        <Fragment>
            {video.url.length ? <video onPlay={handleVideoLoad} src={video.url} controls>
                {subtitle.data ? <track src={URL.createObjectURL(subtitle.data)} kind="subtitles" label={subtitle.title} default={true} /> : ''}
            </video> : ''}
        </Fragment>
    );
}

export default withRouter(Player);