import { useState } from 'react';

import srt2webvtt from '../../helpers/srtConverter';

function Subtitle(props) {
    const { subtitle, setSubtitle } = props;

    const handleSubtitleSelect = (e) => {
        const file = e.target.files[0];
        if (file) {
            var fr = new FileReader();

            fr.readAsText(file, 'windows-1256');

            fr.onload = function () {
                const blob = new Blob([srt2webvtt(fr.result)], { type: 'text/vtt' });
                setSubtitle({ title: file.fileName, data: blob });
            }
        }
    };

    return (
        <div className="card p-4 mt-5">
            <p>فایل SRT زیرنویس رو از سایت های دانلود زیرنویس دانلود کن و از سیستمت انتخاب کن تا رو پلیر فیلم برات نمایش بدیم</p>
            <div className="file-input-container">
                <input type="file" onChange={handleSubtitleSelect} />
                <button className="btn btn-primary mx-0 mb-0">
                    {subtitle.data ? 'جایگزین کردن زیرنویس' : 'افزودن فایل زیرنویس به فیلم'}
                </button>
            </div>
        </div>
    );
}

export default Subtitle;