const localStorageHelper = {
    addVideo: video => {
        const videos = localStorage.getItem('videos') ? JSON.parse(localStorage.getItem('videos')) : [];
        localStorage.setItem('videos', JSON.stringify([...videos, video]));
        console.log(video);
    },
    removeVideo: video => {
        let videos = localStorage.getItem('videos') ? JSON.parse(localStorage.getItem('videos')) : [];
        if (videos.length) {
            const videoIndex = videos.findIndex(v => v == video);
            delete videos[videoIndex];
            localStorage.setItem('videos', JSON.stringify(videos.filter(v => !!v)));
        }
    },
    getVideos: () => {
        const videos = localStorage.getItem('videos') ? JSON.parse(localStorage.getItem('videos')) : [];
        return videos;
    }
};

export default localStorageHelper;