import { Fragment, useState } from 'react';
import { withRouter } from 'react-router-dom';

import History from './home/history';

function Home(props) {
    const [url, setUrl] = useState('');

    const handlePlayButton = () => {
        props.history.push(`/p/${url}`);
    };

    return (
        <Fragment>
            <h1>رایگان بی‌دردسر آنلاین ببین!</h1>
            <p className="mt-3 mb-5">کافیه فقط لینک دانلود فیلمی که می‌خوای استریم کنی رو وارد کنی</p>

            <div className="player-wrapper">
                <input type="text" placeholder="لینک دانلود فیلم را وارد نمایید..." value={url} onChange={e => setUrl(e.target.value)} />
            </div>
            <div className="d-flex align-items-center justify-content-center">
                <button className="btn btn-primary" onClick={handlePlayButton}>
                    <span>پخش آنلاین</span>
                </button>
            </div>

            <History />

            <p className="mt-5 text-center">به‌خاطر داشته باشید که توانایی استریم فرمت‌های مختلف به نسخه و نوع مرورگر شما برمی‌گردد. در حال حاضر ویدیو‌های x265 پشتیبانی نمی‌شوند.</p>
        </Fragment>
    );

}
export default withRouter(Home);