import { Fragment, useState } from 'react';

import Player from './player/player';
import Subtitle from './player/subtitle';

function Play(props) {
    const [subtitle, setSubtitle] = useState({
        data: null,
        title: ''
    });

    const [video, setVideo] = useState({
        url: props.location.pathname.substr(3, props.location.pathname.length),
        title: ''
    });

    return (
        <Fragment>
            <Player url={video.url} setUrl={url => setVideo({ ...video, url: url })} video={video} subtitle={subtitle} />
            <Subtitle subtitle={subtitle} setSubtitle={setSubtitle} />
        </Fragment>
    );
}

export default Play;