import { useState } from 'react';
import { withRouter } from 'react-router-dom';

import localStorageHelper from '../../helpers/localStorage';

function History(props) {
    const [videos, setVideos] = useState(localStorageHelper.getVideos());

    const handleRemoveVideo = video => {
        localStorageHelper.removeVideo(video);
        let newVideos = [...videos];
        const videoIndex = newVideos.findIndex(v => v == video);
        delete newVideos[videoIndex];
        setVideos(newVideos.filter(v => !!v));
    };

    return (
        videos.length ? <div className="card p-4 w-100">
            <strong className="h6 font-weight-bold mb-3">ویدیو‌هایی که در گذشته دیده‌اید...</strong>
            {videos.map(v =>
                <div className="d-flex align-items-center justify-content-between flex-row-reverse prev-video">
                    <strong>{v}</strong>
                    <div className="d-flex align-items-center justify-content-end flex-row-reverse">
                        <i className="far fa-play ml-5 cursor-pointer" onClick={() => props.history.push(`/p/${v}`)}></i>
                        <i className="far fa-trash cursor-pointer" onClick={() => handleRemoveVideo(v)}></i>
                    </div>
                </div>
            )}
        </div> : ''
    );
}

export default withRouter(History);