import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import './App.scss';
import Logo from './assets/images/logo.png';

import Home from './components/home';
import Play from './components/play';

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
        <div className="app-inner">
          <div className="app-header">
            <div className="d-flex align-items-start justify-content-center flex-column">
              <div>
                <img src={Logo} alt="Stream Instead Of Downloading..." style={{ position: 'relative', top: -10, marginRight: 15 }} width={64} />
                <strong className="font-weight-bold">Siod!</strong>
              </div>
              <p>Stream Instead Of Downloading...</p>
            </div>
          </div>
          <div className="app-center">
            <Switch>
              <Route path="/" exact={true} component={Home} />
              <Route path="/p/:url" component={Play} />
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;
